import React, { useEffect, useState } from 'react';
import { xmlData } from '../xml_data';
import FilterForm from './FilterForm';
import Pagination from './Pagination';
import Table from './Table';

const Messages = () => {
    const [mails, setMails] = useState([]);
    const [filtredMails, setFiltredMails] = useState(mails);
    const [loading, setLoading] = useState(false);
    const [currentPage, setCurrentPage] = useState(1);
    const [mailsPerPage, setMailsPerPage] = useState(10);

    //Get current mails
    const indexOfLastMail = currentPage * mailsPerPage;
    const indexOfFirstMail = indexOfLastMail - mailsPerPage;
    const currentMails = filtredMails.slice(indexOfFirstMail, indexOfLastMail);
    const totalPages = Math.ceil(filtredMails.length / mailsPerPage);
    const [phoneNumber, setPhoneNumber] = useState('');
    const [duration, setDuration] = useState({ value: 'all', label: '' });
    //Set filter options

    const [period, setPeriod] = useState({
        value: 'allPeriod',
        label: ' ',
    });
    

    const periodOptions = [
        { value: 'allPeriod', label: ' ' },
        { value: '2016-12', label: '  2016 ' },
        { value: '2016-11', label: '  2016 ' },
        { value: '2016-10', label: '  2016 ' },
        { value: '2016-09', label: '  2016 ' },
        { value: '2016-08', label: '  2016 ' },
        { value: '2016-07', label: '  2016 ' },
        { value: '2016-06', label: '  2016 ' },
        { value: '2016-05', label: '  2016 ' },
        { value: '2016-04', label: '  2016 ' },
        { value: '2016-03', label: '  2016 ' },
        { value: '2016-02', label: '  2016 ' },
        { value: '2016-01', label: '  2016 ' },
        { value: '2015', label: ' 2015 ' },
    ];
    const durationOptions = [
        { value: 'all', label: '' },
        { value: 60, label: ' 1 ' },
        { value: 180, label: ' 3 ' },
        { value: 300, label: ' 5 ' },
    ];

    const paginate = (numberPage) => {
        setCurrentPage(numberPage);
    };
    const incPagePaginate = () => {
        if (currentPage >= totalPages) {
            setCurrentPage(1);
        } else {
            setCurrentPage((preState) => preState + 1);
        }
    };
    const decPagePaginate = () => {
        if (currentPage <= 1) {
            setCurrentPage(totalPages);
        } else {
            setCurrentPage((preState) => preState - 1);
        }
    };

    const handleResetFilter = () => {
        setFiltredMails(mails);
        setPeriod({ value: 'allPeriod', label: ' ' });
        setDuration({ value: 'all', label: '' })
        setPhoneNumber('')
    };

    useEffect(() => {
        //for fetching data from server:
        // setLoadind(true)
        // fetch(Url).then(resp => resp.text().then(data=> {
        //     const { XMLParser } = require('fast-xml-parser');
        //     const parser = new XMLParser();
        //     let jObj = parser.parse(data);
        // setMails(jObj.Root.Data);
        // }))
        // setLoadind(false)

        const { XMLParser } = require('fast-xml-parser');

        const parser = new XMLParser();
        let jObj = parser.parse(xmlData);
        setMails(jObj.Root.Data);
    }, []);

    useEffect(() => {
        setFiltredMails(mails);
    }, [mails]);

    // set filter by phoneNumber
    useEffect(()=>{
        if (!phoneNumber) {
            setFiltredMails(filtredMails);
            return;
        }

        setFiltredMails(filtredMails.filter(item => item.From.includes(phoneNumber)))
    }, [phoneNumber])

    //set filter by duration
    useEffect(() => {
        if (duration.value === 'all') {
            setFiltredMails(filtredMails);
            return;
        }
        setFiltredMails(filtredMails.filter((item) => item.Duration <= duration.value));
    }, [duration.value]);

    // set filter by period
    useEffect(() => {
        if (period.value === 'allPeriod') {
            setFiltredMails(mails);
            return;
        }
        setFiltredMails(
            mails.filter(
                (item) =>
                    Date.parse(item.Received) >=
                        Date.parse(`${period.value}-01`) &&
                    Date.parse(item.Received) <=
                        Date.parse(`${period.value}-31`)
            )
        );
        setCurrentPage(1);
    }, [period.value]);

    return (
        <div className='container content'>
            <FilterForm
                period={period}
                setPeriod={setPeriod}
                periodOptions={periodOptions}
                duration={duration}
                setDuration={setDuration}
                durationOptions={durationOptions}
                phoneNumber={phoneNumber}
                setPhoneNumber={setPhoneNumber}
                handleResetFilter={handleResetFilter}
            />
            <Table mails={currentMails} loading={loading} />
            <Pagination
                mailsPerPage={mailsPerPage}
                totalMails={filtredMails.length}
                currentPage={currentPage}
                paginate={paginate}
                incPagePaginate={incPagePaginate}
                decPagePaginate={decPagePaginate}
            />
        </div>
    );
};

export default Messages;
